import { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import { Alert, InputGroup, Form, Nav } from 'react-bootstrap';
import { useDispatch, useSelector } from 'react-redux';
import { addItem, addCart } from '../store'
import {useLike} from '../hooks/like.js'

function Detailpages(props) {

    let [count, setCount] = useState(true)
    let [warning, setwarning] = useState(false)
    let [num, setNum] = useState('')
    let [tab, setTab] = useState(0)

    let [like, addLike] = useLike()


    let dispatch = useDispatch()

    let { id } = useParams();
    let goods = props.goods

    let truegoods = goods.filter((good) => {
        return good.id == id;
    })

    let idforid = truegoods[0].id + 1

    useEffect(() => {
        if (isNaN(num) == true) {
            setwarning(true)
        }


    }, [num])

    localStorage.setItem('watched', JSON.stringify(truegoods[0].id));


    return (
        <div className="container">
            {
                count == true ? <Alert key='info' variant='info'>
                    주문하기 전 이달의 구매 혜택을 누려보세요 !
                </Alert> : null
            }

            <div className="row">
                <div className="col-md-6">
                    <img src={'https://codingapple1.github.io/shop/shoes' + idforid + '.jpg'} width="100%"></img>
                </div>
                <div className="col-md-6">
                    <h4 className="pt-5">{truegoods[0]?.title}</h4>
                    <p>{truegoods[0].content}</p>
                    <p>{truegoods[0].price}</p>


                    {like} <span onClick={() => {
                        addLike()
                    }}>❤️</span>

                </div>
                {
                    warning == true ? <Alert key='danger' variant='danger'>
                        숫자만 입력하세요 !
                    </Alert> : null
                }
                <InputGroup className="mb-3">
                    <InputGroup.Text id="inputGroup-sizing-default">
                        할인코드
                    </InputGroup.Text>
                    <input onChange={(e) => { setNum(e.target.value) }} />
                </InputGroup>


                <button className="btn btn-danger" onClick={() => {
                    dispatch(addCart({ id: truegoods[0].id, name: truegoods[0].name, count: truegoods[0].count }))
                }}>주문하기</button>
            </div>

            <Nav variant="underline" defaultActiveKey="/link0">
                <Nav.Item>
                    <Nav.Link eventKey="/link0" onClick={() => {
                        setTab(0)
                    }}>상품 상세 설명</Nav.Link>
                </Nav.Item>
                <Nav.Item>
                    <Nav.Link eventKey="/link1" onClick={() => {
                        setTab(1)
                    }}>가격이 너무 비싸요</Nav.Link>
                </Nav.Item>
                <Nav.Item>
                    <Nav.Link eventKey="/link2" onClick={() => {
                        setTab(2)
                    }}>환불 정책</Nav.Link>
                </Nav.Item>
            </Nav>

            <TabContent tab={tab}></TabContent>

        </div >
    );
}

function TabContent(props) {
    let tab = props.tab

    let [fade, setFade] = useState('')

    useEffect(() => {
        setTimeout(() => {
            setFade('end')
        }, 100)

        return () => {
            setFade(' ')
        }
    }, [props])

    if (tab == 0) {
        return (
            <div className={`start ${fade}`}>이 신발은 어쩌구 저쩌구 어디서부터 왔으며 가격은 얼마이며 ..</div>
        )
    }
    if (tab == 1) {
        return (
            <div className={`start ${fade}`}>그럼 사지 말던가</div>
        )
    }
    if (tab == 2) {
        return (
            <div className={`start ${fade}`}>샀으면 장땡이지 뭔 환불</div>
        )
    }
}


export default Detailpages;